<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">用户管理</el-row>
    <el-row type="flex" justify="start" style="margin-top: 10px;">
      <el-button plain disabled>导出用户数据<i class="el-icon-download el-icon--right"></i></el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="infomationList" style="width: 100%">
        <el-table-column label="图像" >
          <template slot-scope="scope">
            <img :src="scope.row.image" style="width: 40px; height: 40px;background-color: #eee;">
          </template>
        </el-table-column>
        <el-table-column prop="title" label="微信名" />
        <el-table-column prop="desc" label="open Id" show-overflow-tooltip />
        <el-table-column prop="public_at" label="手机号" show-overflow-tooltip />
        <el-table-column prop="public_at" label="地址" show-overflow-tooltip />
        <el-table-column label="我的总关注数" show-overflow-tooltip />
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="10"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total" />
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      infomationList: [],
      curPage: 1,
      pageSize: 20,
      total: 100
    }
  },

  components: {
  },

  methods: {
    // 获取信息列表
    queryList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      // need fix
      // 缺少用户 api
      this.$http.informationList(params).then((res) => {
        if (!res) return
        this.infomationList = res.data.result_rows.informations
        this.total = res.data.result_rows.page.total_count
      })
    },
    // page大小改变
    handleSizeChange (page) {
      this.pageSize = page
      this.queryList()
    },
    // 当前page改变
    handleCurrentChange (page) {
      this.curPage = page
      this.queryList()
    }
  },
  created () {
    this.queryList()
  }
}
</script>

